.contacts-card {
  width: fit-content;
  color: white;
  font-family: 'Righteous', cursive;
  text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 20px #ff0080, 0 0 30px #ff0080, 0 0 40px #ff0080, 0 0 55px #ff0080, 0 0 75px #ff0080;
  padding: 60px 60px 30px 60px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  border: 0.2rem solid #fff;
  border-radius: 20px;
  background-color: #00000099;
  box-shadow: 0 0 0.5rem #fff, inset 0 0 0.5rem #fff, 0 0 2rem var(--neon-border-color), inset 0 0 2rem var(--neon-border-color), 0 0 4rem var(--neon-border-color), inset 0 0 4rem var(--neon-border-color); }
  .contacts-card .links {
    display: flex;
    justify-content: center; }
  .contacts-card .header {
    font-family: 'Vibur', cursive;
    font-size: 55px;
    text-align: center;
    margin-bottom: 48px; }
  .contacts-card .link {
    margin: 12px;
    font-size: 30px;
    font-family: 'neoneonregular', serif;
    color: #fff;
    text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 20px #ff0080, 0 0 30px #ff0080, 0 0 40px #ff0080, 0 0 55px #ff0080, 0 0 75px #ff0080;
    cursor: pointer;
    text-decoration: none; }
    .contacts-card .link:hover {
      text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 40px #ff00c9, 0 0 60px #ff00c9, 0 0 80px #ff00c9, 0 0 100px #ff00c9, 0 0 120px #ff00c9; }
  @media (max-width: 810px) {
    .contacts-card {
      font-size: 40px;
      text-align: center;
      width: 400px;
      padding: 30px; } }
  @media (max-width: 540px) {
    .contacts-card {
      font-size: 24px;
      text-align: center;
      width: 240px; }
      .contacts-card .header {
        font-size: 45px;
        text-align: center;
        margin-bottom: 0; }
      .contacts-card .links {
        display: grid; }
      .contacts-card .link {
        margin: 6px;
        font-size: 24px; } }
